body {
  background: var(--wp--preset--gradient--cool-to-warm-spectrum);
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

* {
  box-sizing: border-box;
}

/* Elements */
img {
  height: auto;
  max-width: 100%;
}

p,
ul {
  line-height: 1.5;
}

a {
  color: var(--wp--preset--color--vivid-cyan-blue);
  text-decoration: none;
  transition: all 0.2s ease;
}
a:hover {
  color: var(--wp--preset--color--dark-cyan-blue);
  text-decoration: underline;
}

.wp-site-blocks {
  background: var(--wp--preset--color--white);
  border-top: 2px solid var(--wp--preset--color--light-gray);
  margin: 2rem auto;
  max-width: 1000px;
}

.site-header {
  padding: 2rem 0 0;
}
.site-header .alignwide {
  padding: 0 4rem;
}

.site-main {
  max-width: 80%;
  margin: 2rem auto;
}

.site-footer {
  border-top: 1px solid var(--wp--preset--color--light-gray);
  padding: 2rem 0 0;
  margin-top: 6rem;
}
.site-footer .alignwide {
  padding: 0 4rem;
}

.colophon {
  background: var(--wp--preset--color--off-white);
  border-top: 1px solid var(--wp--preset--color--light-gray);
  padding: 1rem 0;
}

.wp-block-search {
  text-align: right;
}
.wp-block-search .wp-block-search__inside-wrapper {
  display: block;
}
.wp-block-search .wp-block-search__label {
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;
}
.wp-block-search .wp-block-search__button {
  display: none;
}
.wp-block-search .wp-block-search__input {
  background: url(../assets/images/search.png) no-repeat 0.25em center;
  border: 1px solid var(--wp--preset--color--light-gray);
  border-radius: 2px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.1);
  float: right;
  font-size: 1rem;
  height: 2em;
  line-height: 1.2;
  margin: 1.5rem 0 0 0;
  padding: 0.25em 0.25em 0.25em 1.75em;
  transition: all 400ms ease;
  width: 120px;
}
.wp-block-search .wp-block-search__input::placeholder {
  color: var(--wp--preset--color--mid-gray);
}
.wp-block-search .wp-block-search__input:focus {
  background-color: var(--wp--preset--color--off-white);
  background-position: 0.5em center;
  border: 1px solid var(--wp--preset--color--dark-gray);
  outline: none;
  padding-left: 2em;
  width: 300px;
  height: 3em;
}

.wp-block-site-title {
  margin-bottom: 0;
}
.wp-block-site-title a {
  color: var(--wp--preset--color--dark-grey);
  position: relative;
  text-decoration: none;
}
.wp-block-site-title a::after {
  background: var(--wp--preset--color--white);
  bottom: 0;
  content: "";
  height: 3px;
  left: 0;
  position: absolute;
  transition: all 0.2s ease;
  width: 1%;
}
.wp-block-site-title a:hover::after {
  background: var(--wp--preset--color--pale-cyan-blue);
  width: 100%;
}

.wp-block-site-tagline {
  background-color: var(--wp--preset--gradient--cool-to-warm-spectrum);
  color: var(--wp--preset--color--dark-grey);
  letter-spacing: 0.05em;
  margin-top: 0;
}

.wp-block-navigation .wp-block-navigation-item__content {
  padding-bottom: 3px;
  position: relative;
  text-decoration: none;
}
.wp-block-navigation .wp-block-navigation-item__content::after {
  background: var(--wp--preset--color--white);
  bottom: 0;
  content: "";
  height: 3px;
  left: 0;
  position: absolute;
  transition: all 0.2s ease;
  width: 1%;
}
.wp-block-navigation .wp-block-navigation-item__content:hover::after {
  background: var(--wp--preset--color--pale-cyan-blue);
  width: 100%;
}
.wp-block-navigation .wp-block-navigation-item__content[aria-current=page] {
  cursor: default;
}
.wp-block-navigation .wp-block-navigation-item__content[aria-current=page]::after {
  background: var(--wp--preset--color--vivid-purple);
  width: 100%;
}

.wp-block-rss li {
  line-height: 1.1;
  margin-bottom: 0.5rem;
  transition: background-color 0.2s ease;
}
.wp-block-rss li:hover {
  background-color: var(--wp--preset--color--off-white);
}

.wp-block-rss__item-publish-date {
  color: var(--wp--preset--color--mid-gray);
  font-style: italic;
}

.wp-block-social-links li.custom a {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  height: 36px;
  width: 36px;
}
.wp-block-social-links li.custom a svg {
  display: none;
}
.wp-block-social-links li.wp-social-link-lastfm a {
  padding: 0.35em 0.25em 0.25em 0.35em;
}
.wp-block-social-links li.circlecube a {
  background-image: url("data:image/png;base64,");
}
.wp-block-social-links li.nomaderwhere a {
  background-image: url("data:image/png;base64,");
}
.wp-block-social-links li.wasmormon a {
  background-image: url("data:image/png;base64,");
}
.wp-block-social-links li.itunes a {
  background-image: url("data:image/png;base64,");
}
.wp-block-social-links li.slack a {
  background-image: url(data:image/png;base64,);
  background-size: 24px 24px;
}

.resume-experience-block {
  border-bottom: 1px solid var(--wp--preset--color--light-gray);
  margin: 1rem 0;
  padding: 1rem;
  transition: background-color 0.3s ease;
}
.resume-experience-block:hover, .resume-experience-block:focus {
  background: var(--wp--preset--color--off-white);
  border-bottom: 1px solid var(--wp--preset--color--vivid-green-cyan);
}
.resume-experience-block .job-title {
  margin-top: 1.575rem;
}

.is-style-resume-skills {
  list-style: none;
  padding: 0;
}
.is-style-resume-skills li {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 0 10% 1rem;
}
.is-style-resume-skills li strong {
  font-weight: 400;
  order: 0;
}
.is-style-resume-skills li::before {
  border-bottom: dotted 2px var(--wp--preset--color--light-gray);
  content: "";
  display: block;
  margin: 0 1rem;
  order: 1;
  width: 100%;
}
.is-style-resume-skills li em {
  color: var(--wp--preset--color--luminous-vivid-orange);
  font-style: normal;
  order: 2;
}

.block-talk {
  border: 2px solid var(--wp--preset--color--light-gray);
  border-radius: 3px;
  margin-top: 2rem;
}
.block-talk:hover .block-talk-inner {
  background-color: var(--wp--preset--color--off-white);
}

.block-talk-inner {
  background: var(--wp--preset--color--white);
  display: flex;
  flex-direction: row-reverse;
  padding: 20px 40px 20px 20px;
  position: relative;
  transition: background-color 0.2s ease;
}
@media (max-width: 640px) {
  .block-talk-inner {
    flex-direction: column;
    align-items: center;
  }
}

.block-talk-label {
  background: var(--wp--preset--gradient--cool-to-warm-spectrum);
  background-attachment: fixed;
  background-size: 100vw 100vh;
  color: var(--wp--preset--color--white);
  font-weight: 700;
  left: 20px;
  margin-bottom: 0;
  padding: 17px 20px;
  position: absolute;
  text-transform: uppercase;
  top: -25px;
  width: auto;
}

.block-talk-inner h2.block-talk-title {
  margin-top: 2.25rem;
}

.block-talk-content {
  flex-grow: 2;
}

.block-talk-button-group {
  display: flex;
}
.block-talk-button-group .block-talk-button:first-child {
  border-radius: 2px 0 0 2px;
  border-right-width: 0;
}
.block-talk-button-group .block-talk-button:last-child {
  border-radius: 0 2px 2px 0;
  border-left-width: 0;
}
.block-talk-button-group .block-talk-button {
  align-items: center;
  background-color: var(--wp--preset--color--vivid-cyan-blue);
  border: 0;
  box-shadow: inset 0 0 0 1px var(--wp--preset--color--dark-gray);
  color: var(--wp--preset--color--white);
  cursor: pointer;
  display: inline-flex;
  font-size: 15px;
  font-weight: 400;
  height: 36px;
  justify-content: left;
  margin: 0;
  padding: 8px 12px;
  position: relative;
  text-align: center;
  text-decoration: none;
  text-shadow: none;
  transition: all 0.2s linear;
  white-space: nowrap;
  word-break: break-word;
  z-index: 1;
}
.block-talk-button-group .block-talk-button:hover {
  background-color: var(--wp--preset--color--dark-cyan-blue);
}
.block-talk-button-group .block-talk-button .dashicon {
  margin-right: 8px;
}

.block-talk-links label {
  align-items: baseline;
  display: flex;
}
.block-talk-links label > em {
  min-width: 10%;
  opacity: 0.5;
}

.block-talk-image-wrap {
  position: relative;
  margin: 2rem 2rem 0 0;
  text-align: center;
}
@media (max-width: 640px) {
  .block-talk-image-wrap {
    margin: 2rem auto 0;
  }
}

.block-talk-image {
  background-color: var(--wp--preset--color--vivid-green-cyan);
  border-radius: 50%;
  color: var(--wp--preset--color--white);
  font-size: 150px;
  height: 100%;
  line-height: 200px;
  object-fit: cover;
  object-position: center;
  max-height: 200px;
  max-width: 200px;
  min-height: 200px;
  min-width: 200px;
  width: 100%;
}

.block-talk-image-replace,
.block-talk-image-remove {
  bottom: 2px;
  position: absolute;
  right: 2px;
}

.block-talk-image-replace {
  right: 2.4rem;
}

.block-skills {
  list-style: none;
  padding: 0;
}

.block-skill {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 0 10% 1rem;
}
.block-skill strong {
  font-weight: 400;
  order: 0;
  padding: 0 1rem 0 0;
  white-space: nowrap !important;
}
.block-skill::before {
  border-bottom: dotted 2px var(--wp--preset--color--light-gray);
  content: "";
  display: block;
  margin: 0;
  order: 1;
  width: 100%;
}
.block-skill .block-skill-level {
  color: var(--wp--preset--color--luminous-vivid-orange);
  font-style: normal;
  order: 2;
  padding: 0 0 0 1rem;
  white-space: nowrap !important;
}
.block-skill > .components-base-control {
  order: 2;
  padding: 0;
}
.block-skill > .components-base-control select {
  min-width: 120px;
}